//  -------------------------------------------------------------------->

declare namespace  Flow{
    // --
    interface FlowStepAttr{
        col_list?: string[]
        can_revoke?: number
        auth_list?: any
        filter_by?: any
    }
    // --
    interface FlowStepStru{
        NodeType?: string
        opt?: rSu.NodeOpt
        c?: any
        label?: any
    }
    // - 
    interface FlowStepStr{
        code?: string                // 
        name?: string                // 
        type?: number
        prev?: string
        next?: string
        attr?: FlowStepAttr
        _struct?: FlowStepStru
    }
}

//  --------------------------------------------------------------------||

//  -------------------------------------------------------------------->

// 
declare namespace Dance {
    // 
    interface Tool{
        containerIst?: any        
        startIst?: any
        startTxtIst?: any
        operaIst?: any
        operaTxtIst?: any
        judgeIst?: any
        judgeTxtIst?: any
        endIst?: any
        endTxtIst?: any
        arrowIst?: any
        arrowTxtIst?: any
        textInst?: any
    }
}
// 
declare namespace rSu{
    // 
    interface P{
        x: number
        y: number
    }
    // 
    interface NodeOpt{
        cx?: number 
        cy?: number 
        w?: number 
        h?: number
        text?: string
    }
}

//  --------------------------------------------------------------------||

class NodeBase{
    fromLine: any
    toLine: any
    NodeType: string
    opt: rSu.NodeOpt
    label: any
    c: any
    constructor(){
        // 
        this.fromLine = []
        this.toLine = []
        this.NodeType = null            // 
    }    
    /**
     * 
     * @param {stirng} type 
     * @param {this}  $node 
     */
    recordLine(type: string, $node: any){
        if('from' == type){
            this.fromLine.push($node)
        }
        else if('to' == type){
            this.toLine.push($node)
        }
    }
    /**
     * 
     * @param {function} callback 
     */
    syncLineMove(callback: any){
        if('function' !== typeof callback){
            callback = (instance: any, type: any) => {}
        }
        // 
        var fLines = this.fromLine        
        var tLines = this.toLine
        // 
        for(var i=0; i<fLines.length; i++){
            var $fC = fLines[i].c
            var $fPath = $fC.attr('path')
            callback($fC, 'from', fLines[i])
        }
        // 
        for(var j=0; j<tLines.length; j++){
            var $tC = tLines[j].c
            callback($tC, 'to', tLines[j])
        }
    }
    /**
     * 
     * NodeBase struct to json   struct 
     * @returns {object}
     */
    toJson(){
        var _struct: Flow.FlowStepStru = {
            NodeType: this.NodeType,            // 
            opt: this.opt,                      // 
            c: {
                attr: this.c.attr()              // 
            }            
        }
        if(this.label){                         // 
            _struct.label = {
                attr: this.label.attr()
            }
        }
        return _struct
    }
}
export default NodeBase


